import React from 'react';

const Blog = ({blogs, setBlogs, blog, title, content, id, image})=>{

    const imageBlog = require.context('./images/',true,/\.jpg$/);
    let img_blog = imageBlog(`./${image}.jpg`).default;
    return(
        <div className="blog_section">
            <h1>{title}</h1>
            <div className="blog_content">
                <img src={img_blog} alt={image} />
                <h3>{content}</h3>
            </div>
        </div>
    );
    
}

export default Blog;